
import { mapState } from 'vuex'

export default {
	name: 'Clients',
    computed: {
        ...mapState(['clients'])
    },
    methods: {
        estiloCard(item){
            const tiempo = item % 3
            switch (tiempo) {
            case 1:
                return '0.0s'
            case 2:
                return '0.1s'
            case 0:
                return '0.2s'
            default:
                return '0.0s'
            }
        }
    }
}
